import Vue from 'vue'
import VueRouter from 'vue-router'
// import Login from '@/components/Login'
// import Home from '@/components/Home'
// import Welcome from '@/components/Welcome'
// import Users from '@/components/user/Users'
// import Rights from '@/components/power/Rights'
// import Roles from '@/components/power/Roles'
// import Goods from '@/components/goods/goods'
// import Params from '@/components/goods/params'
// import Categories from '@/components/goods/categories'
// import Add from '@/components/goods/Add'
// import Orders from '@/components/order/orders'
// import Reports from '@/components/reports/reports'

Vue.use(VueRouter)

const routes = [
  { path: '/', component: () => import(/* webpackChunkName:"login_home_welcome" */ '@/components/Login') },
  { path: '/login', component: () => import(/* webpackChunkName:"login_home_welcome" */ '@/components/Login') },
  {
    path: '/welcome',
    component: () => import(/* webpackChunkName:"login_home_welcome" */ '@/components/Home'),
    children: [
      { path: '', component: () => import(/* webpackChunkName:"login_home_welcome" */ '@/components/Welcome') },
      { path: '/users', component: () => import(/* webpackChunkName:"users" */ '@/components/user/Users') },
      { path: '/rights', component: () => import(/* webpackChunkName:"power" */ '@/components/power/Rights') },
      { path: '/roles', component: () => import(/* webpackChunkName:"power" */ '@/components/power/Roles') },
      { path: '/goods', component: () => import(/* webpackChunkName:"goods" */ '@/components/goods/goods') },
      { path: '/goods/add', component: () => import(/* webpackChunkName:"goods" */ '@/components/goods/Add') },
      { path: '/params', component: () => import(/* webpackChunkName:"goods" */ '@/components/goods/params') },
      { path: '/categories', component: () => import(/* webpackChunkName:"goods" */ '@/components/goods/categories') },
      { path: '/orders', component: () => import(/* webpackChunkName:"order" */ '@/components/order/orders') },
      { path: '/reports', component: () => import(/* webpackChunkName:"reports" */ '@/components/reports/reports') }
    ]
  }
]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  const token = window.localStorage.getItem('token')
  if (to.path === '/login') {
    next()
  } else {
    if (!token) {
      next('/login')
    } else {
      next()
    }
  }
})

export default router
